<template>
  <main-layout>

    <NewGames title="New Games" :posts="getAllNewPosts" />

    <Pagination />
  </main-layout>
</template>

<script>
// @ is an alias to /src
import { mapGetters, mapActions } from 'vuex'

import MainLayout from '../layout/MainLayout.vue'
import NewGames from '../components/universal/NewGames.vue'
import Pagination from '../components/universal/Pagination.vue'
// import RecomendedSkeleton from '../components/home/RecomendedSkeleton'


export default {
  name: 'Home',
  metaInfo: {
    title: 'Vue App YaYoYe',
    meta: [
      { vmid: 'description', property: 'description', content: 'Vue App YaYoYe' },
      { vmid: 'og:title', property: 'og:title', content: 'Vue App YaYoYe' },
      { vmid: 'og:description', property: 'og:description', content: 'Vue App YaYoYe' },
    ],
  },
  components: {
    MainLayout, 
    NewGames,
    Pagination,
    // RecomendedSkeleton
  },
  methods: {
    ...mapActions(['fetchAllNewPosts'])
  },
  async mounted(){
    await this.fetchAllNewPosts({page: this.$route.params.id, per_page: 44})
    
  },

  computed: {
    ...mapGetters([ 'getAllNewPosts']),
  },
}
</script>
